import TopCategoriesServer from "../TopCategories/server";
import Banner from "./Banner";
import Contactus from "@/Components/Contactus";
import ConnectPlugin from "@/Components/connectPlugins";

export default async function HomepageLayout({ token, addonList, bannerList }) {
  return (
    <>
      <TopCategoriesServer />

      <Banner bannerList={bannerList?.data} />

      {addonList &&
        addonList?.["widget"] &&
        addonList?.["widget"]?.status == 1 &&
        ConnectPlugin.PromotionalWidget && (
          <ConnectPlugin.PromotionalWidget token={token} />
        )}

      <div className="pt-8 sm:pt-28">
        <Contactus token={token} />
      </div>
    </>
  );
}
