import { headers } from "next/headers";
import { GetAddonsList, GetToken } from "@/utilities/cookies";
import HomepageLayout from "@/Components/HomepageLayout";
import { getBannerList } from "@/api/serversideaction/category";

import createStructuredData from "@/Components/Seo/createStructuredData";
import StructuredData from "@/Components/Seo/StructuredData";
import axiosInstance from "@/api/interceptor";

async function getStoreSettingsData() {
  return await axiosInstance.get(`settings/store-setting`);
}

export default async function Home() {
  const [token, addonList, bannerList, storeSettingsArray] = await Promise.all([
    GetToken(),
    GetAddonsList(),
    getBannerList(),
    getStoreSettingsData(),
  ]);

  const headersList = await headers();
  const host = headersList.get("host");
  const protocol = headersList.get("x-forwarded-proto") || "https";
  const autoBaseUrl = `${protocol}://${host}`;
  const storeSettings = storeSettingsArray?.data?.data || [];
  const setting = storeSettings?.[0] || {};

  //  Social links pulled safely from store settings
  const socialLinks = [
    setting?.facebook,
    setting?.instagram,
    setting?.linkedin,
    setting?.twitter,
    setting?.youtube,
  ].filter(Boolean); // remove null / undefined / empty

  // Create structured data
  const schema = createStructuredData({
    title: setting?.metaTitle || setting?.storeName || "Default Title",
    description: setting?.metaTagDescription || "Default description",
    url: setting?.storeUrl || autoBaseUrl,

    image: setting?.storeLogoName
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${setting.storeLogoPath}&name=${setting.storeLogoName}&width=1200&height=630`
      : `${autoBaseUrl}/img/logo.svg`,

    dateCreated: setting?.createdDate,
    dateModified: setting?.modifiedDate,

    organizationName: setting?.storeName || "Default Organization",
    organizationLogo: setting?.storeLogoName
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${setting.storeLogoPath}&name=${setting.storeLogoName}`
      : `${autoBaseUrl}/img/logo.svg`,

    organizationUrl: setting?.storeUrl || autoBaseUrl,

    //  Inject social links
    customData: {
      sameAs: socialLinks,
    },
  });

  return (
    <>
      <StructuredData data={schema} />
      <HomepageLayout token={token} addonList={addonList} bannerList={bannerList} />
    </>
  );
}
